import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton } from 'react-mdl';
import { Link, hashHistory } from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.css';
import './css/a.css';
import logo from './components/logo.png';
import Main from './components/main';
import './App.css';



class App extends Component {
  render() {
    return (
      <hashHistory basename="/gamarra-ar">
        <div class="container">
          <div class="row">
            <div class="col">
              <img src={logo} class="img-fluid" />
            </div>
          </div>
          <div class="row" className="mb-2">
            <div class="col text-center">
              <Button component={Link} className="btn btn-primary btn-lg btn-block" to="/arlocation">AR</Button>
            </div>
          </div>
          <div class="row" className="mb-2">
            <div class="col text-center">
              <Button component={Link} className="btn btn-primary btn-lg btn-block" to="/maplocation">Mapa</Button>
            </div>
          </div>
        </div>
      </hashHistory>
    );

  }
}

export default App;


